// Base and Accent Colors
$earth-deep-forest: #2e7d32; // Rich forest green, ideal for footers and accents
$earth-midnight-blue: #283593; // Deep blue, great for prominent CTAs and headers
$earth-sunset-orange: #ef6c00; // Vibrant orange, perfect for highlights and buttons
$earth-clay-red: #d84315; // Warm red, suitable for warnings or attention elements
$earth-sandstone: #fff3e0; // Light sandy color, excellent for backgrounds
$earth-mountain-stone: #3e2723; // Almost black, great for text and contrast details

// Secondary Accent Colors
$earth-sky-blue: #4fc3f7; // Soft blue for informational call-outs
$earth-grass-green: #9ccc65; // Light green for success messages and icons
$earth-golden-sun: #ffca28; // Golden yellow for ratings or favorite icons

// Neutral Tones
$earth-paper-white: #fafafa; // Very light gray, almost white, for general background
$earth-smoky-gray: #546e7a; // Medium gray, versatile for secondary text and borders
$earth-charcoal: #37474f; // Dark gray, perfect for primary text and headings

